import { ref } from 'vue';
import { showToast } from 'vant';

// 歌单详情 - 歌单详情动态 - 歌单所有歌曲
import { getPlaylistDetail, getPlaylistDetailDynamic, getPlaylistTrackAll } from '../../../api/api.js';
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'detail',
  data() {
    return {
      id: '',
      nickname: '',
      avatarUrl: '',
      playList: [],
      dynamic: [],
      songList: []
    };
  },
  setup() {
    // 显示分享面板
    const showShare = ref(false);
    const options = [[{
      name: '微信',
      icon: 'wechat'
    }, {
      name: '朋友圈',
      icon: 'wechat-moments'
    }, {
      name: '微博',
      icon: 'weibo'
    }, {
      name: 'QQ',
      icon: 'qq'
    }], [{
      name: '复制链接',
      icon: 'link'
    }, {
      name: '分享海报',
      icon: 'poster'
    }, {
      name: '二维码',
      icon: 'qrcode'
    }, {
      name: '小程序码',
      icon: 'weapp-qrcode'
    }]];
    const onSelect = option => {
      showToast(option.name);
      showShare.value = false;
    };
    return {
      options,
      onSelect,
      showShare
    };
  },
  created() {
    this.initGet();
  },
  methods: {
    left() {
      showToast('返回');
      this.$router.go(-1);
    },
    right() {
      showToast('搜索');
    },
    download() {
      showToast('下载');
    },
    collect() {
      showToast('收藏');
    },
    all() {
      showToast('播放全部');
    },
    mv() {
      showToast('mv');
    },
    more() {
      showToast('更多');
    },
    async initGet() {
      // 传值
      this.id = this.$router.currentRoute.value.query.id;
      // 歌单详情
      const {
        data: detail
      } = await getPlaylistDetail(this.id);
      // console.log('歌单详情')
      // console.log(detail)
      this.nickname = detail.playlist.creator.nickname;
      this.avatarUrl = detail.playlist.creator.avatarUrl;
      this.playList = detail.playlist;
      sessionStorage.setItem('detail', JSON.stringify(detail));
      // 歌单详情动态
      const {
        data: dynamic
      } = await getPlaylistDetailDynamic(this.id);
      // console.log('歌单详情动态')
      // console.log(dynamic)
      this.dynamic = dynamic;
      sessionStorage.setItem('dynamic', JSON.stringify(dynamic));
      // 歌单所有歌曲
      const {
        data: songs
      } = await getPlaylistTrackAll(this.id);
      // console.log('歌单所有歌曲')
      // console.log(songs)
      this.songList = songs.songs;
      sessionStorage.setItem('songs', JSON.stringify(songs));
    },
    changeCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(1) + '亿';
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + '万';
      }
    },
    playMusic(i) {
      const musicItem = this.songList[i];
      this.$store.commit('updatePlayList', [musicItem]);
      this.$store.commit('updatePlayListIndex', i - i);
    }
  }
};